import React from "react";
import Box from "@mui/material/Box";
import SpeedDial from "@mui/material/SpeedDial";
import SpeedDialAction from "@mui/material/SpeedDialAction";
import FileCopyIcon from "@mui/icons-material/FileCopyOutlined";
import SaveIcon from "@mui/icons-material/Save";
import PrintIcon from "@mui/icons-material/Print";
import ShareIcon from "@mui/icons-material/Share";
import LogoSVG from "./logosvg";
import CloseIcon from "@mui/icons-material/Close";
import { blue } from "@mui/material/colors";

const actions = [
  { icon: <FileCopyIcon fontSize="large" />, name: "Copy" },
  { icon: <SaveIcon fontSize="large" />, name: "Save" },
  { icon: <PrintIcon fontSize="large" />, name: "Print" },
  { icon: <ShareIcon fontSize="large" />, name: "Share" },
];

interface MainButtonProps {
  o: any;
}

const MainButton = (props: MainButtonProps) => {
  const { open, setOpen } = props.o;

  let idx = -1;
  const handleOpen = () => setOpen(true);
  const handleClose = () => {
    idx = -1;
    setOpen(false);
  };
  const PLUS_WIDTH = 72;
  const calculate_addition_width = (index: number) => {
    if (index === 1) {
      return -4;
    } else if (index === 2) {
      return 4;
    }
    return 0;
  };
  const calculate_height = (index: number) => {
    if (index === 2 || index === 1) {
      return -45;
    }
    return 0;
  };

  return (
    <Box>
      <SpeedDial
        ariaLabel="Main Button"
        direction="down"
        icon={
          open ? (
            <CloseIcon
              color="primary"
              sx={{ color: "#fafafa", fontSize: 100 }}
            />
          ) : (
            <LogoSVG sx={{ fontSize: 100 }} />
          )
        }
        onClose={handleClose}
        onOpen={handleOpen}
        FabProps={{ sx: { width: 200, height: 200 } }}
      >
        {actions.map((action) => {
          idx++;
          return (
            <SpeedDialAction
              sx={{
                position: "absolute",
                right: idx * PLUS_WIDTH - 70 + calculate_addition_width(idx),
                bottom: calculate_height(idx),
                width: 100,
                height: 100,
                backgroundColor: blue[50],
              }}
              key={action.name}
              icon={action.icon}
              tooltipTitle={action.name}
            />
          );
        })}
      </SpeedDial>
    </Box>
  );
};

export default MainButton;
